@media (width< 973px){
    a {
        display: none;
    }
    .btnMenu:hover {

        cursor: pointer;
        .line{
            position: relative;
        }
        #line1{
            display: none;
        }
        #line2{
            position: absolute;
            transition: 1s;
            margin-bottom: 0;
            transform: rotateZ(45deg);
        }
        #line3{
            transition: 1s;
            transform: rotateZ(-45deg);
        }
    }
    
}




